<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="partyTableData" :noDataText="partyTableNoDataText" :sst="true" search @search="filterPartyTable" @sort="orderPartyTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadPartyTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="partyTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ partyTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="partyTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="partyId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="partyName">
            {{ this.$t('PARTY_NAME') }}
          </vs-th>
          <vs-th sort-key="partyType">
            {{ this.$t('TYPE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].partyId">
              {{ data[index].partyId }}
            </vs-td>
            <vs-td :data="data[index].partyName">
              <span :class="disabledClass(data[index])">{{ data[index].partyName }}</span>
            </vs-td>
            <vs-td :data="data[index].partyTypeDescription">
              {{ data[index].partyTypeDescription }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="partyTableTotalPages" v-model="partyTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import PartyTableMixin from '@/mixins/nucleus/PartyTableMixin'

export default {
  name: 'party-popup',
  mixins: [PopupMixin, PartyTableMixin],
  mounted: function () {
    this.loadPartyTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
