<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 required">
          <label class="vs-input--label">{{ $t('APP') }}</label>
          <vs-select autocomplete class="w-full" v-model="limit.appId">
            <vs-select-item v-for="(app, index) in apps" :key="index" :value="app.appId" :text="app.appName"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TRANSFER_TYPE') }}</label>
          <funds-transfer-type-lookup :title="$t('TRANSFER_TYPE')" :recordValue="limit.transferTypeId" :recordDisplay="limit.transferTypeName" valueField="typeId" displayField="typeName" @select-record="selectTransferTypeRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('FREQUENCY') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="limit.frequencyUomId">
            <vs-select-item v-for="(uom, index) in frequencies" :key="index" :value="uom.uomId" :text="uom.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('CURRENCY_TYPE') }}</label>
          <div class="flex list-style-none p-2">
            <li class="left mr-4" v-for="(enumeration, index) in currencyTypes" :key="index">
              <vs-radio v-model="limit.currencyTypeEnumId" :vs-value="enumeration.enumId">{{ enumeration.description }}</vs-radio>
            </li>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('AMOUNT') }}</label>
          <div class="flex">
            <vs-input-number v-model="limit.minAmount" min="0"/>
            <span class="mt-2 mx-4"> - </span>
            <vs-input-number v-model="limit.maxAmount" min="0"/>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="limit.currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('DEBIT_CURRENCY') }}</label>
          <currency-lookup :title="$t('DEBIT_CURRENCY')" :recordValue="limit.debitCurrencyId" :recordDisplay="limit.debitCurrencyDescription" valueField="uomId" displayField="description" @select-record="selectDebitCurrencyRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TRANSFER_CURRENCY') }}</label>
          <currency-lookup :title="$t('TRANSFER_CURRENCY')" :recordValue="limit.transferCurrencyId" :recordDisplay="limit.transferCurrencyDescription" valueField="uomId" displayField="description" @select-record="selectTransferCurrencyRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="limit.limitTypeEnumId === 'LMT_TYPE_USER'">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('USER') }}</label>
          <user-lookup :title="$t('USER')" :recordValue="limit.userId" :recordDisplay="limit.userFullName" valueField="userId" displayField="userFullName" @select-record="selectUserRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="limit.limitTypeEnumId === 'LMT_TYPE_USER_GRP'">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('USER_GROUP') }}</label>
          <user-group-lookup :title="$t('USER_GROUP')" :recordValue="limit.userGroupId" :recordDisplay="limit.userGroupDescription" valueField="userGroupId" displayField="description" @select-record="selectUserGroupRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="limit.limitTypeEnumId === 'LMT_TYPE_ORG'">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('PARTY') }}</label>
          <party-lookup :title="$t('PARTY')" :recordValue="limit.partyId" :recordDisplay="limit.organizationName" valueField="partyId" displayField="partyName" @select-record="selectPartyRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="limit.limitTypeEnumId === 'LMT_TYPE_ACCT'">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('ACCOUNT') }}</label>
          <vs-input class="w-full" v-model="limit.accountId"/>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="limit.limitTypeEnumId === 'LMT_TYPE_BEN'">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('BENEFICIARY') }}</label>
          <vs-input class="w-full" v-model="limit.beneficiaryId"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="limit.fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="limit.toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
          <vs-input class="w-full" v-model="limit.description"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <vs-checkbox v-model="limit.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyLimit('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyLimit('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyLimit('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LimitService from '@/services/limit/LimitService'
import AppService from '@/services/commons/AppService'
import EnumerationService from '@/services/commons/EnumerationService'
import UomService from '@/services/commons/UomService'
import Datepicker from 'vuejs-datepicker'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import UserLookup from '@/views/nucleus/user/UserLookup'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import FundsTransferTypeLookup from '@/views/ft/funds-transfer-type/FundsTransferTypeLookup'

export default {
  name: 'limit-modify',
  mixins: [AlertMixin, DatePickerMixin],
  components: {
    Datepicker,
    CurrencyLookup,
    UserLookup,
    UserGroupLookup,
    PartyLookup,
    FundsTransferTypeLookup
  },
  data: function () {
    return {
      limit: {},
      apps: [],
      appId: '',
      frequencies: [],
      currencyTypes: []
    }
  },
  methods: {
    async load () {
      let self = this
      LimitService.fundsTransferLimits({
        limitId: this.$route.params.id
      }).then(function (response) {
        self.limit = response.data.fundsTransferLimitList[0] || {}
        self.limit.disabled = self.limit.disabled === 'Y'
      })

      AppService.apps({
      }).then(function (response) {
        self.apps = response.data.appList || []
      })

      UomService.uoms({
        uomTypeEnumId: 'UT_TIME_FREQ_MEASURE',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.frequencies = response.data.uomList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferCurrencyType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.currencyTypes = response.data.enumerationList || []
      })
    },
    selectTransferCurrencyRecord (record) {
      this.limit.transferCurrencyId = record.uomId || ''
    },
    selectDebitCurrencyRecord (record) {
      this.limit.debitCurrencyId = record.uomId || ''
    },
    selectUserRecord (record) {
      this.limit.userId = record.userId || ''
    },
    selectUserGroupRecord (record) {
      this.limit.userGroupId = record.userGroupId || ''
    },
    selectPartyRecord (record) {
      this.limit.partyId = record.partyId || ''
    },
    selectTransferTypeRecord (record) {
      this.limit.transferTypeId = record.typeId || ''
    },
    modifyLimit (nextAction) {
      let self = this
      LimitService.modifyFundsTransferLimit({
        limitId: this.limit.limitId,
        appId: this.limit.appId,
        transferTypeId: this.limit.transferTypeId,
        frequencyUomId: this.limit.frequencyUomId,
        currencyTypeEnumId: this.limit.currencyTypeEnumId,
        transferCurrencyId: this.limit.transferCurrencyId,
        debitCurrencyId: this.limit.debitCurrencyId,
        minAmount: this.limit.minAmount,
        maxAmount: this.limit.maxAmount,
        fromDate: this.getTime(this.limit.fromDate),
        toDate: this.getTime(this.limit.toDate),
        description: this.limit.description,
        disabled: this.limit.disabled ? 'Y' : 'N',
        userId: this.limit.userId,
        userGroupId: this.limit.userGroupId,
        partyId: this.limit.partyId,
        accountId: this.limit.accountId,
        beneficiaryId: this.limit.beneficiaryId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewLimit', params: { id: response.data.limitId } })
          } else {
            self.$router.push({ name: 'BrowseLimits' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
